.App{
  .navbar{
    height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(4, 112, 119);
    input{
      width: 200px;
      height: 30px;
      border-radius: 10px;
      border: none;
      padding: 5px;
    }
    .icon{
      width: 50px;
      height: 50px;
      cursor: pointer;
      color: #fff;

    }
  }
 .cards{

   display: grid;
   grid-template-columns: auto auto auto auto;
   .user-card{
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     width: 320px;
     height: 400px;
     border-radius: 10px;
     background-color: rgb(3, 101, 88);
     gap: 10px 10px;
     column-gap: 20px;
     margin: 10px;
     padding: 10px;
     text-align: center;
     color: aliceblue;
     span{
      color: gold;
     }
     .image{
       width: 150px;
       height: 200px;
     }
   }
 }
}